import {
  $
} from '@mfelibs/base-utils';
import {
  browserType
} from './browser';

/**
 * @param {*} config.cardSelector cardcard 
 * @param {*} config.boxSelector img
 * @param {*} config.imgSelector imgboxSelectorimg
 * @param {*} config.defaultCss  
 */

class weidongCard {
  constructor(config) {
    if (config == undefined) {
      config = {
        cardSelector: '.weidong_card',
        boxSelector: '.weidong_box',
        imgSelector: '.weidong_img',
        defaultCss: true
      };
    }
    this.config = config;
    this.cH = document.documentElement.clientHeight; //       clientHeight
    this.allCardList = [];
    this.bottomBarHeight = config.bottomBarHeight || 0;
    this.topBarHeight = config.topBarHeight || 0;
    this.needDegrade = (browserType.isUC() && browserType.isIOS()) || browserType.isQQ();
    this.init();
  }
  init() {
    this.addCard(this.config);
    if (!this.needDegrade) {
      this.move();
      this.listener();
    }
  }
  addCard(config) {
    if (config == undefined) {
      config = this.config
    }
    let {
      cardSelector,
      boxSelector,
      imgSelector = 'img',
      defaultCss
    } = config;
    let allCard = $(cardSelector); // card
    for (let i = 0, len = allCard.length; i < len; i++) {
      let thisCardInfo = {};
      let adDom = allCard.eq(i);
      if (adDom.hasClass('___init_sina_weidong')) {
        continue;
      } else {
        adDom.addClass('___init_sina_weidong');
      }
      let imgBoxDom = adDom.find(boxSelector); // img
      if (imgBoxDom.length == 0 && cardSelector == boxSelector) {
        imgBoxDom = adDom;
      }
      let imgDom = imgBoxDom.find(imgSelector); // img

      if (defaultCss) {
        if (this.needDegrade) {
          imgBoxDom.css({
            paddingTop: '3rem',
            position: 'relative',
            overflow: 'hidden',
            boxSizing: 'border-box'
          });
        } else {
          imgBoxDom.css({
            paddingTop: '37.5%',
            position: 'relative',
            overflow: 'hidden',
            boxSizing: 'border-box'
          });
        }
        adDom.find('.weidong_wrap').css({
          position: 'absolute',
          bottom: 0,
          width: '100%',
          verticalAlign: 'top',
          overflow: 'hidden'
        });
        imgDom.css({
          transition: 'transform 100ms',
          webkitTransition: 'transform 100ms',
          width: '100%'
        })
      }

      thisCardInfo.adDom = adDom // card
      thisCardInfo.imgDom = imgDom // img
      thisCardInfo.aH = adDom.height(); // card   adHeight   
      thisCardInfo.bH = imgBoxDom.height(); // img   boxHeight
      thisCardInfo.mH = imgDom.height(); // img
      thisCardInfo.imgDom.on('load', () => {
        thisCardInfo.mH = imgDom.height(); // 
      })
      this.allCardList.push(thisCardInfo);
    }
  }
  move() {
    let cH = this.cH;
    let tH = document.documentElement.offsetHeight; // totalHeight  body   
    let bottomBarHeight = this.bottomBarHeight;
    let topBarHeight = this.topBarHeight;
    this.allCardList.forEach((cardInfo) => {
      let aT = cardInfo.adDom.offset().top; // card  adTop  
      let aH = cardInfo.aH;
      let bH = cardInfo.bH;
      let mH = cardInfo.mH;
      let min = (aT - cH + aH + bottomBarHeight) < 0 ? 0 : (aT - cH + aH + bottomBarHeight); // 
      let max = aT > (tH - cH) ? (tH - cH - topBarHeight) : (aT - topBarHeight); //   
      let duration = max - min;
      if (duration < 100 || mH <= bH) { //   ||    
        cardInfo.imgDom.css('transform', 'translate3d(0, 0, 0)');
        return
      }
      let moveScale = (mH - bH) / duration; // img/

      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop <= min) {
        scrollTop = min
      }
      if (scrollTop > max) {
        scrollTop = max
      }
      cardInfo.imgDom.css('transform', 'translate3d(0, ' + (scrollTop - min) * moveScale + 'px, 0)');
    })
  }
  listener() {
    var debounce = new Date().getTime();
    $(window).on('scroll', () => {
      if (new Date().getTime() - debounce < 50) {
        return false
      } else {
        debounce = new Date().getTime()
      }
      this.move();
    })
  }
}

export default weidongCard;


